import React, { Component } from "react";
import { NavLink } from "react-bootstrap";
import HomeData from "../Home/HomeData";

const Intro = () => {
  return HomeData.map((val) => {
    if (val.id % 2 != 0) {
      return (
        <React.Fragment>
          <section className="HomeP">
            <div className="homeContainer1">
              <div className="intromessage">
                <NavLink exact to={val.link}>
                  {val.title}
                </NavLink>
                {val.aboutIt}
              </div>
              <div className="homeImg">
                <img src={val.imgSrc}></img>
              </div>
            </div>
          </section>
        </React.Fragment>
      );
    } else {
      return (
        <React.Fragment>
          <section className="HomeP">
            <div className="homeContainer2">
              <div className="homeImg">
                <img src={val.imgSrc}></img>
              </div>
              <div className="intromessage">
                <NavLink exact to={val.link}>
                  {val.title}
                </NavLink>
                {val.aboutIt}
              </div>
            </div>
          </section>
        </React.Fragment>
      );
    }
  });
};

export default Intro;



