import React from "react";
import { NavLink } from "react-bootstrap";
import RobotData from "./RobotData";

const RobotContainer = () => {
  return RobotData.map((val) => {
    if (val.id % 2 !== 0) {
      return (
        <React.Fragment>
          <section>
            <div className="containerT1">
              <div className="imagediv">
                <NavLink to="/...">
                  <img src={val.imgSrc}></img>
                </NavLink>
              </div>
              <div className="right">
                <div className="descrptn">
                  <h1>{val.title}</h1>
                  {val.aboutIt}
                </div>
              </div>
            </div>
          </section>
        </React.Fragment>
      );
    } else {
      return (
        <React.Fragment>
          <section>
            <div className="containerT2">
              <div className="left">
                <div className="descrptn">
                  <h1>{val.title}</h1>
                  {val.aboutIt}
                </div>
              </div>
              <div className="imagediv">
                <NavLink to="/...">
                  <img src={val.imgSrc}></img>
                </NavLink>
              </div>
            </div>
          </section>
        </React.Fragment>
      );
    }
  });
};
export default RobotContainer;



