import React from "react";
import { Route, Switch } from "react-router-dom";
import Navbar from "./Navbar";
import RobotContainer from "./Robots/RobotContainer";
import Title from "./Robots/Title";
import Intro from "../components/Home/Intro";
import ImageSlider from "./Home/ImageSlider";
import Error from "./Error";
import RelativePos from "./Home/RelativePos";
import FinalImage from "./Home/FinalImage";

const App = () => {
  return (
    <React.Fragment>
      <Switch>
        <Route
          exact
          path="/stdrobot"
          render={(props) => (
            <React.Fragment>
              <Navbar />
              <ImageSlider />
              <RelativePos />
              <Intro />
              <FinalImage />
            </React.Fragment>
          )}
        />
        <Route
          path="/robots"
          render={(props) => (
            <React.Fragment>
              <Navbar />
              <Title />
              <RobotContainer />
            </React.Fragment>
          )}
        />
        <Route component={Error} />
      </Switch>
    </React.Fragment>
  );
};
export default App;



