import React, { Component } from "react";
import pooper from "../images/robopop.jpg";

const FinalImage = () => {
  return (
    <React.Fragment>
      <div className="final">
        <div className="largeimg">
          <img src={pooper}></img>
        </div>
      </div>
    </React.Fragment>
  );
};
export default FinalImage;



