import React, { Component, useRef } from "react";
import { NavLink } from "react-router-dom";
import logo from "./images/logo.png";

class Navbar extends Component {
  constructor(props) {
    super(props);
  }
  state = {
    navclasses: "homee",
    navLinkOpen: false,
  };

  // this handles the navbar toggle by adding the active class when cliked on hamburger
  handleNavlinkToggle = () => {
    this.setState({ navLinkOpen: !this.state.navLinkOpen });
  };

  renderClasses = () => {
    let classes = "navlinks ";
    if (this.state.navLinkOpen) {
      classes += "active";
    }
    return classes;
  };

  // navbar should disappear if it loses focus

  handleOnBlur = () => {
    if (this.state.navLinkOpen === false) {
    } else {
      this.setState({ navLinkOpen: !this.state.navLinkOpen });
    }
  };

  //nav element in navbar should have class 'navbar' when it's out of home page,otherwise class should be 'home'
  // this is done because of different style of navbar in home and other pages...

  updateNavbarClasses = () => {
    this.setState({ navclasses: "navbar" });
  };

  render() {
    return (
      <React.Fragment>
        <nav className={this.state.navclasses}>
          <div
            onClick={() => {
              this.setState({ navclasses: "homee" });
            }}
            className="logo"
          >
            <NavLink exact activeClassName="active_link" to="/stdrobot">
              <img src={logo} alt=""></img>
            </NavLink>
          </div>
          <div
            tabIndex="-1"
            onClick={this.handleNavlinkToggle}
            onBlur={this.handleOnBlur}
            className="hamburger"
          >
            <div className="line"></div>
            <div className="line"></div>
            <div className="line"></div>
          </div>
          <ul className={this.renderClasses()}>
            <li>
              <NavLink
                onClick={this.updateNavbarClasses}
                exact
                activeClassName="active_link"
                to="/robots"
              >
                ROBOTS
              </NavLink>
            </li>
            <li>
              <NavLink
                onClick={this.updateNavbarClasses}
                exact
                activeClassName="active_link"
                to="/joinus"
              >
                JOIN US
              </NavLink>
            </li>
            <li>
              <NavLink
                onClick={this.updateNavbarClasses}
                exact
                activeClassName="active_link"
                to="/getinvolved"
              >
                GET INVOLVED
              </NavLink>
            </li>
          </ul>
        </nav>
      </React.Fragment>
    );
  }
}
export default Navbar;



