import React from "react";
import { NavLink } from "react-bootstrap";

import snake from "../images/snk.jpg";
import pooper from "../images/robopop.jpg";
import wheely from "../images/2Wheely.jpg";
import arm from "../images/robotoe.jpg";
const RobotData = [
  {
    id: 1,
    imgSrc: snake,
    title: "Snakeino",
    aboutIt: (
      <p>
        Snakeino is a modern hardware design of snake game executed in a
        LED-matrix board making it playable by using joystick. To know more,
        <NavLink to="/...">click here.</NavLink>
      </p>
    ),
  },
  {
    id: 2,
    imgSrc: pooper,
    title: "Popper",
    aboutIt: (
      <p>
        Popper is a simple combat robot designed for the event(Robopop) which
        was organized on LOCUS. To know more,
        <NavLink to="/...">click here.</NavLink>
      </p>
    ),
  },
  {
    id: 3,
    imgSrc: wheely,
    title: "2Wheely",
    aboutIt: (
      <p>
        2Wheely is a auto balancing bot on 2 wheels made by using MPU-6050
        executing PID control mechanism. To know more,
        <NavLink to="/...">click here.</NavLink>
      </p>
    ),
  },
  {
    id: 4,
    imgSrc: arm,
    title: "RoboToe",
    aboutIt: (
      <p>
        RoboToe is a Tic-Tac-Toe playing robotic arm made using Image Processing
        library of python.To know more,
        <NavLink to="/...">click here.</NavLink>
      </p>
    ),
  },
];
export default RobotData;



