import { Carousel } from "react-bootstrap";
import React, { Component } from "react";
import snake from "../images/snk.jpg";
import pooper from "../images/robopop.jpg";
import wheely from "../images/2Wheely.jpg";
import arm from "../images/robotoe.jpg";
import wheckt from "../images/2wheelckt.jpg";
import snkckt from "../images/snkckt1.jpg";
const ImageSlider = () => {
  return (
    <React.Fragment>
      <Carousel controls={false} pause={false}>
        <Carousel.Item interval={2000}>
          <img src={wheely} alt="First slide" />
        </Carousel.Item>
        <Carousel.Item interval={2000}>
          <img src={snake} alt="Second slide" />
        </Carousel.Item>
        <Carousel.Item interval={2000}>
          <img src={pooper} alt="THird slide" />
        </Carousel.Item>
        <Carousel.Item interval={2000}>
          <img src={arm} alt="Fourth slide" />
        </Carousel.Item>
        <Carousel.Item interval={2000}>
          <img src={wheckt} alt="Fifth slide" />
        </Carousel.Item>
        <Carousel.Item interval={2000}>
          <img src={snkckt} alt="sixth slide" />
        </Carousel.Item>
      </Carousel>
    </React.Fragment>
  );
};

export default ImageSlider;



